.outbound-details {
    background-color: var(--color2);
    border-radius: 10px;
    width: 100%;
    color: var(--thinLine)
}

.outbound-detail-summary-reply {
    display: flex;
    justify-content: space-between;
}

.outbound-email-details {
    display: flex;
    justify-content: space-between;
    padding: 10px;


}

.outbound-allocated-email {
    width: 70%;
    padding: 2px;
    overflow-x: auto;
}

.outbound-allocated-email-capacity {
    width: 30%;
    padding: 2px;
    text-align: right;
    overflow-x: auto;
}


.individaul-messages {

    padding-left: 30px;
    display: flex;
    flex-direction: column;
    gap: 4px;
    font-size: 13px;
}

.reply-date>p {
    text-align: right;

}

.lab {
    width: 50px;
}



.reply-from,
.reply-to,
.reply-body {
    display: flex;
    flex-direction: column;
    background-color: var(--color1);
    gap: 20px;
    padding: 8px;
    border-radius: 6px;
}



.reply-controls {
    display: flex;
    gap: 15px
}

.reply-controls>div {
    width: 35%;
    background-color: var(--color1);
    padding: 8px;
    border-radius: 6px;
    text-align: center;
}

.reply-refresh {
    padding-top: 20px;
}

@media screen and (max-width:750px) {
    .reply-content {
        overflow-x: scroll;
    }
}