.completed {
    color: rgb(101, 150, 27)
}

.pending {
    color: rgb(184, 91, 57)
}

.show-tasks>ul>li {
    padding: 10px;
    border: 1px solid var(--thinLine);


}

.task-highlight {
    padding: 10px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border-radius: 10px;
    margin-bottom: 3px;
    overflow-x: auto;

}
 
